import { randomElement } from './random';


export interface Speech {
    speak(phrase: string, context: object): Promise<void> | void;
    speakRandomPhrase(phrases: string[], context: object): Promise<void> | void;
}


export class BrowserSpeech implements Speech {
    private utterance: SpeechSynthesisUtterance;

    constructor() {
        this.utterance = new SpeechSynthesisUtterance();
        this.initializeVoice();
    }

    private initializeVoice() {
        const initVoices = () => {
            const voices = window.speechSynthesis.getVoices();
            if (voices.length > 0) {
                const femaleVoice = voices.find(voice => voice.name.toLowerCase().includes('female'));
                if (femaleVoice) {
                    this.utterance.voice = femaleVoice;
                }
                this.utterance.rate = 0.9;
                this.utterance.pitch = 1.1;
            }
        };

        // Try to get voices immediately
        initVoices();

        // Also listen for the voiceschanged event
        window.speechSynthesis.addEventListener('voiceschanged', initVoices);
    }

    speak(phrase: string, context: object) {
        window.speechSynthesis.cancel(); // Cancel any ongoing speech
        this.utterance.text = phrase;
        window.speechSynthesis.speak(this.utterance);
    }

    speakRandomPhrase(phrases: string[], context: object) {
        const phrase = randomElement(phrases);
        this.speak(phrase, context);
    }
}


export class MockSpeech implements Speech {
    transcript: string[] = [];

    reset() {
        this.transcript = [];
    }

    speak(phrase: string, context: object) {
        this.transcript.push(phrase);
    }

    speakRandomPhrase(phrases: string[], context: object) {
        if (phrases.length === 0) {
            return;
        }

        const phrase = randomElement(phrases);
        this.speak(phrase, context);
    }
}


export default function getSpeech(env: string = process.env.NODE_ENV!): Speech {
    if (env === 'test') {
        return new MockSpeech();
    } else {
        return new BrowserSpeech();
    }
}
